import React, {useEffect} from "react";

function isScriptLoaded(url) {
  const scripts = document.getElementsByTagName('script');
  for (let i = scripts.length; i--;) {
      if (scripts[i].src == url) return true;
  }
  return false;
}

function loadTag(adsData) {
  const { id, size, id2 } = adsData;
  try {
    window._mNHandle.queue.push(function () {
      window._mNDetails.loadTag(id, size, id2);
    });
  }
  catch (error) {
  }
}

export default function Template({
	adsData
}) {
  useEffect(() => {
    const scriptUrl = "https://contextual.media.net/dmedianet.js?cid=8CUI6S7IJ";
    const isLoaded = isScriptLoaded(scriptUrl);
    console.log({isLoaded});

    if (!isLoaded) {
      window._mNHandle = window._mNHandle || {};
      window._mNHandle.queue = window._mNHandle.queue || [];
      const medianet_versionId = "3121199";
      const script = document.createElement("script");
      script.src = scriptUrl;
      script.async = true;
      script.onload = () => {
        loadTag(adsData);
      }
      document.body.appendChild(script);
    } else {
      loadTag(adsData);
    }

    return () => {
    };
  });
  return null;
}
